___CSS_LOADER_EXPORT___.push([module.id, `.game-section.battle {
  justify-content: center;
  gap: 120px;
  margin-top: 30px;
}

.turn {
  position: absolute;
  bottom: 15%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2rem;
  font-weight: bold;
}

.game-section.battle.active.blurred .turn {
  display: none;
}

.player-one-section,
.player-two-section {
  min-width: fit-content;
  display: flex;
  gap: 60px;
}

.fleet-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5px;
}

.fleet-header {
  height: 40px;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  font-weight: bold;
}

.fleet {
  border: 2px solid black;
  border-radius: 5px;
  width: 250px;
  height: 250px;
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  padding: 25px;
}

.fleet-ship-container {
  display: flex;
  height: 20px;
}

.ship-part {
  width: 20px;
  height: 20px;
  background-color: blue;
}

.part-sunken {
  background-color: red;
}

.revealed-shot {
  background-color: rgb(187, 1, 1);
  opacity: 0.6;
}
